.banned-users-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

input[type="text"] {
  width: 98%;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 16px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f5f5f5;
  font-weight: bold;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

tbody tr:hover {
  background-color: #ebebeb;
}